<template>
  <div>个人中心</div>
</template>

<script>
import {instance2} from '@/utils/http'
import { mapMutations } from 'vuex';
export default {
  methods:{
    ...mapMutations('TabbarStore',['changeIsTabbar']),
  },
  created(){
    this.changeIsTabbar(true)
  },
  beforeRouteEnter (to, from, next) {
    instance2.post('/api/user/isloginin').then(res=>next()).catch(err=>next('/login'))
  },
}

</script>
